/* Combined Gallery */

.combined-gallery {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'SF Pro Display', 'Helvetica Neue', sans-serif;
}

.gallery-header {
  text-align: center;
  margin-bottom: 40px;
}

.gallery-header h1 {
  font-size: 3rem;
  color: #1d1d1f;
  margin-bottom: 15px;
  font-weight: 700;
  background: linear-gradient(135deg, #2c3e50 0%, #3498db 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.gallery-header p {
  font-size: 1.2rem;
  color: #4a5568;
  margin: 0;
}

/* Tab Navigation */
.tab-navigation {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 40px;
  flex-wrap: wrap;
}

.tab-button {
  background: #f8fafc;
  color: #4a5568;
  border: 2px solid #e2e8f0;
  padding: 15px 30px;
  border-radius: 25px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  font-size: 1rem;
  font-family: 'Inter', sans-serif;
}

.tab-button:hover {
  background: #f1f5f9;
  border-color: #0071e3;
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(0, 113, 227, 0.15);
}

.tab-button.active {
  background: linear-gradient(135deg, #0071e3, #0056b3);
  color: white;
  border-color: #0071e3;
  box-shadow: 0 4px 15px rgba(0, 113, 227, 0.3);
}

/* Tab Content */
.tab-content {
  background: white;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0,0,0,0.08);
  border: 1px solid rgba(0,0,0,0.05);
  overflow: hidden;
}

.tab-panel {
  padding: 30px;
}

/* Flickr Photos Section */
.flickr-photos-section h3 {
  font-size: 2rem;
  color: #1d1d1f;
  margin-bottom: 25px;
  text-align: center;
  font-weight: 600;
}

.flickr-photos-embed {
  margin-bottom: 30px;
  background: #f8fafc;
  border-radius: 12px;
  padding: 20px;
  border: 1px solid #e2e8f0;
  text-align: center;
}

.flickr-photos-embed a {
  display: inline-block;
  text-decoration: none;
  transition: transform 0.3s ease;
}

.flickr-photos-embed a:hover {
  transform: scale(1.02);
}

.flickr-photos-embed img {
  box-shadow: 0 4px 20px rgba(0,0,0,0.1);
  transition: box-shadow 0.3s ease;
}

.flickr-photos-embed a:hover img {
  box-shadow: 0 8px 30px rgba(0,0,0,0.15);
}

/* Simple animation for the Flickr embed */
.flickr-photos-embed {
  animation: gentle-pulse 4s ease-in-out infinite;
}

@keyframes gentle-pulse {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.02);
    opacity: 0.95;
  }
}




/* Upload Section */
.upload-section.minimal {
  text-align: center;
  margin-bottom: 20px;
}

.upload-btn.minimal {
  background: linear-gradient(135deg, #27ae60, #2ecc71);
  color: white;
  padding: 16px 32px;
  border-radius: 25px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  display: inline-block;
  box-shadow: 0 4px 15px rgba(39, 174, 96, 0.3);
  border: none;
  font-size: 1.1rem;
  font-family: 'Inter', sans-serif;
}

.upload-btn.minimal:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(39, 174, 96, 0.4);
}

/* Thanks Section */
.thanks-section {
  text-align: center;
}

.thanks-section h4 {
  font-size: 1.8rem;
  color: #2c3e50;
  margin-bottom: 20px;
  font-weight: 600;
}

.thanks-description {
  font-size: 1.1rem;
  color: #4a5568;
  margin-bottom: 30px;
  line-height: 1.6;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.organizations-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.org-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  padding: 20px;
  background: #f8fafc;
  border-radius: 12px;
  transition: all 0.3s ease;
  border: 2px solid transparent;
}

.org-item:hover {
  background: #f1f5f9;
  border-color: #0071e3;
  transform: translateY(-4px);
  box-shadow: 0 8px 25px rgba(0, 113, 227, 0.15);
}

.org-logo-placeholder {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: linear-gradient(135deg, #0071e3 0%, #0056b3 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 20px rgba(0, 113, 227, 0.3);
}

.org-initial {
  color: white;
  font-size: 2rem;
  font-weight: 700;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.org-name {
  color: #4a5568;
  font-weight: 600;
  font-size: 1rem;
  text-align: center;
}

.thanks-footer {
  border-top: 2px solid #e2e8f0;
  padding-top: 30px;
  margin-top: 30px;
}

.thanks-footer p {
  font-size: 1.1rem;
  color: #4a5568;
  margin-bottom: 20px;
  line-height: 1.6;
}

.learn-more-link {
  display: inline-block;
  background: linear-gradient(135deg, #0071e3, #0056b3);
  color: white;
  padding: 14px 28px;
  border-radius: 25px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 113, 227, 0.3);
}

.learn-more-link:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 113, 227, 0.4);
}

/* Interactive Photo Carousel */
.photo-carousel {
  margin-bottom: 30px;
  background: #f8fafc;
  border-radius: 12px;
  padding: 20px;
  border: 1px solid #e2e8f0;
}

/* No Photos Message */
.no-photos-message {
  text-align: center;
  padding: 60px 20px;
}

.no-photos-message h4 {
  font-size: 1.5rem;
  color: #2c3e50;
  margin-bottom: 15px;
  font-weight: 600;
}

.no-photos-message p {
  color: #4a5568;
  font-size: 1.1rem;
  margin-bottom: 25px;
  line-height: 1.6;
}


.carousel-container {
  position: relative;
  width: 100%;
  height: 500px;
  overflow: hidden;
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0,0,0,0.1);
}

.carousel-track {
  display: flex;
  height: 100%;
  transition: transform 0.6s ease-in-out;
}

.carousel-slide {
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}

.carousel-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
}

.slide-caption {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(transparent, rgba(0,0,0,0.8));
  color: white;
  padding: 20px;
  text-align: center;
  border-radius: 0 0 8px 8px;
}

.slide-caption h4 {
  margin: 0 0 8px 0;
  font-size: 1.3rem;
  font-weight: 600;
}

.slide-caption p {
  margin: 0;
  font-size: 1rem;
  opacity: 0.9;
  line-height: 1.4;
}

/* Navigation Dots */
.carousel-dots {
  position: absolute;
  bottom: 80px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 12px;
  z-index: 10;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 2px solid white;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(0,0,0,0.3);
}

.dot:hover {
  background: rgba(255,255,255,0.5);
  transform: scale(1.2);
}

.dot.active {
  background: white;
  transform: scale(1.3);
}

/* Navigation Arrows */
.carousel-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(255,255,255,0.9);
  border: none;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 24px;
  color: #333;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0,0,0,0.2);
  z-index: 10;
}

.carousel-arrow:hover {
  background: white;
  transform: translateY(-50%) scale(1.1);
  box-shadow: 0 6px 20px rgba(0,0,0,0.3);
}

.carousel-prev {
  left: 20px;
}

.carousel-next {
  right: 20px;
}

/* Carousel Footer */
.carousel-footer {
  text-align: center;
  margin-top: 20px;
}

.view-album-btn {
  display: inline-block;
  background: linear-gradient(135deg, #0071e3, #0056b3);
  color: white;
  padding: 16px 32px;
  border-radius: 25px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 113, 227, 0.3);
  font-size: 1.1rem;
  font-family: 'Inter', sans-serif;
}

.view-album-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 113, 227, 0.4);
  text-decoration: none;
  color: white;
}

/* Responsive Design */
@media (max-width: 768px) {
  .gallery-header h1 {
    font-size: 2.5rem;
  }
  
  .tab-navigation {
    flex-direction: column;
    align-items: center;
  }
  
  .tab-button {
    width: 100%;
    max-width: 300px;
  }
  
  .organizations-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 15px;
  }
  
  .tab-panel {
    padding: 20px;
  }
}

@media (max-width: 480px) {
  .combined-gallery {
    padding: 15px;
  }
  
  .gallery-header h1 {
    font-size: 2rem;
  }
  
  .organizations-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }
} 

/* Responsive Design for Carousel */
@media (max-width: 768px) {
  .carousel-container {
    height: 400px;
  }
  
  .carousel-arrow {
    width: 40px;
    height: 40px;
    font-size: 20px;
  }
  
  .carousel-prev {
    left: 10px;
  }
  
  .carousel-next {
    right: 10px;
  }
  
  .slide-caption {
    padding: 15px;
  }
  
  .slide-caption h4 {
    font-size: 1.1rem;
  }
  
  .slide-caption p {
    font-size: 0.9rem;
  }
} 