/* Comic Book Reader Styles */

.comic-reader {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'SF Pro Display', 'Helvetica Neue', sans-serif;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%);
  min-height: 100vh;
  color: white;
}

.comic-reader.fullscreen {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;
  padding: 0;
  overflow: hidden;
}

/* Comic Loading */
.comic-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 60vh;
  text-align: center;
}

.comic-loading-spinner {
  position: relative;
  width: 80px;
  height: 80px;
  margin-bottom: 30px;
}

.spinner-ring {
  position: absolute;
  width: 100%;
  height: 100%;
  border: 4px solid transparent;
  border-top: 4px solid #00d4ff;
  border-radius: 50%;
  animation: spin 1.5s linear infinite;
}

.spinner-ring:nth-child(2) {
  border-top-color: #ff6b6b;
  animation-delay: 0.5s;
}

.spinner-ring:nth-child(3) {
  border-top-color: #4ecdc4;
  animation-delay: 1s;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.comic-loading h2 {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 15px;
  background: linear-gradient(135deg, #00d4ff, #ff6b6b);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.comic-loading p {
  font-size: 1.3rem;
  color: #b8c5d6;
  margin-bottom: 30px;
}

.comic-loading-details {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.comic-loading-details span {
  background: rgba(255, 255, 255, 0.1);
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 0.9rem;
  color: #b8c5d6;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* Comic Header */
.comic-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 30px;
  padding: 25px;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.05));
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
}

.comic-title h1 {
  font-size: 2.8rem;
  font-weight: 800;
  margin-bottom: 10px;
  background: linear-gradient(135deg, #00d4ff, #ff6b6b);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.comic-subtitle {
  font-size: 1.2rem;
  color: #b8c5d6;
  margin-bottom: 15px;
}

.comic-series-info {
  display: flex;
  gap: 15px;
}

.issue-number {
  background: linear-gradient(135deg, #ff6b6b, #ee5a24);
  color: white;
  padding: 6px 12px;
  border-radius: 15px;
  font-size: 0.9rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.series-name {
  background: linear-gradient(135deg, #00d4ff, #0099cc);
  color: white;
  padding: 6px 12px;
  border-radius: 15px;
  font-size: 0.9rem;
  font-weight: 600;
}

.comic-controls {
  display: flex;
  gap: 15px;
}

.control-btn {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 12px 16px;
  border-radius: 12px;
  cursor: pointer;
  font-size: 1.2rem;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.control-btn:hover {
  background: rgba(255, 255, 255, 0.2);
  border-color: rgba(255, 255, 255, 0.5);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 212, 255, 0.3);
}

/* Comic Content */
.comic-content {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  padding: 30px;
  margin-bottom: 30px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
}

/* Comic Navigation */
.comic-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  padding: 20px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.nav-btn {
  background: linear-gradient(135deg, #00d4ff, #0099cc);
  color: white;
  border: none;
  padding: 15px 25px;
  border-radius: 25px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 1rem;
}

.nav-btn:hover:not(:disabled) {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 212, 255, 0.4);
}

.nav-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  background: #666;
}

.nav-arrow {
  font-size: 1.5rem;
  font-weight: 800;
}

.page-info {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 1.1rem;
  color: #b8c5d6;
}

.page-number, .total-pages {
  background: linear-gradient(135deg, #ff6b6b, #ee5a24);
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-weight: 700;
  font-size: 1.2rem;
}

.page-separator {
  color: #b8c5d6;
  font-size: 1rem;
}

/* Comic Page Container */
.comic-page-container {
  position: relative;
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

.comic-page {
  position: relative;
  max-width: 100%;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  border: 3px solid rgba(255, 255, 255, 0.2);
}

.comic-image {
  width: 100%;
  height: auto;
  max-height: 70vh;
  object-fit: contain;
  display: block;
}

.page-caption {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.9));
  color: white;
  padding: 30px 20px 20px;
  text-align: center;
}

.page-caption h3 {
  font-size: 1.4rem;
  font-weight: 700;
  margin-bottom: 10px;
  color: #00d4ff;
}

.page-caption p {
  font-size: 1rem;
  color: #b8c5d6;
  line-height: 1.5;
}

/* Page Turn Effects */
.page-turn-left, .page-turn-right {
  position: absolute;
  top: 0;
  width: 50px;
  height: 100%;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.1), transparent);
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.page-turn-left {
  left: 0;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.1), transparent);
}

.page-turn-right {
  right: 0;
  background: linear-gradient(90deg, transparent, rgba(0, 0, 0, 0.1));
}

.comic-page-container:hover .page-turn-left,
.comic-page-container:hover .page-turn-right {
  opacity: 1;
}

/* Comic Footer */
.comic-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.reading-progress {
  display: flex;
  flex-direction: column;
  gap: 10px;
  min-width: 200px;
}

.progress-bar {
  width: 100%;
  height: 8px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #00d4ff, #ff6b6b);
  border-radius: 4px;
  transition: width 0.5s ease;
}

.progress-text {
  font-size: 0.9rem;
  color: #b8c5d6;
  text-align: center;
}

.comic-actions {
  display: flex;
  gap: 15px;
}

.action-btn {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 10px 20px;
  border-radius: 20px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.action-btn:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.2);
  border-color: rgba(255, 255, 255, 0.5);
  transform: translateY(-2px);
}

.action-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Comic Thumbnails */
.comic-thumbnails {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  padding: 30px;
  margin-bottom: 30px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
}

.thumbnails-header {
  text-align: center;
  margin-bottom: 25px;
}

.thumbnails-header h3 {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 10px;
  color: #00d4ff;
}

.thumbnails-header p {
  color: #b8c5d6;
  font-size: 1rem;
}

.thumbnails-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  gap: 20px;
  max-height: 400px;
  overflow-y: auto;
  padding: 10px;
}

.thumbnail {
  background: none;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
}

.thumbnail:hover {
  transform: translateY(-5px);
}

.thumbnail.active {
  transform: scale(1.05);
}

.thumbnail-image {
  position: relative;
  border-radius: 12px;
  overflow: hidden;
  margin-bottom: 10px;
  border: 3px solid transparent;
  transition: all 0.3s ease;
}

.thumbnail.active .thumbnail-image {
  border-color: #00d4ff;
  box-shadow: 0 0 20px rgba(0, 212, 255, 0.5);
}

.thumbnail-img {
  width: 100%;
  height: 80px;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.thumbnail:hover .thumbnail-img {
  transform: scale(1.1);
}

.thumbnail-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.thumbnail:hover .thumbnail-overlay {
  opacity: 1;
}

.thumbnail-number {
  color: white;
  font-size: 1.2rem;
  font-weight: 700;
}

.thumbnail-title {
  font-size: 0.8rem;
  color: #b8c5d6;
  line-height: 1.3;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

/* Comic Instructions */
.comic-instructions {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  padding: 25px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.instruction-item {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 15px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.instruction-icon {
  font-size: 1.5rem;
  background: linear-gradient(135deg, #00d4ff, #ff6b6b);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.instruction-item span:last-child {
  color: #b8c5d6;
  font-size: 0.9rem;
}

/* Fullscreen Styles */
.comic-reader.fullscreen .comic-header {
  position: sticky;
  top: 0;
  z-index: 10;
  margin: 0;
  border-radius: 0;
}

.comic-reader.fullscreen .comic-content {
  margin: 0;
  border-radius: 0;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.comic-reader.fullscreen .comic-page-container {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.comic-reader.fullscreen .comic-image {
  max-height: 80vh;
}

/* Responsive Design */
@media (max-width: 768px) {
  .comic-reader {
    padding: 15px;
  }
  
  .comic-header {
    flex-direction: column;
    gap: 20px;
    text-align: center;
  }
  
  .comic-title h1 {
    font-size: 2.2rem;
  }
  
  .comic-navigation {
    flex-direction: column;
    gap: 20px;
  }
  
  .comic-footer {
    flex-direction: column;
    text-align: center;
  }
  
  .comic-actions {
    justify-content: center;
  }
  
  .thumbnails-container {
    grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
    gap: 15px;
  }
  
  .comic-instructions {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .comic-reader {
    padding: 10px;
  }
  
  .comic-title h1 {
    font-size: 1.8rem;
  }
  
  .comic-content {
    padding: 20px;
  }
  
  .comic-navigation {
    padding: 15px;
  }
  
  .nav-btn {
    padding: 12px 20px;
    font-size: 0.9rem;
  }
  
  .thumbnails-container {
    grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
    gap: 10px;
  }
} 